import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";

const StaticTable = ({
  columnHeaders,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
}) => {
  console.log("Mapped Attributes prop:", mappedAttributes);

  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th></th>
          <th>Column Name</th>
          <th>Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => (
          <tr key={index}>
            <td>
              <input
                type="checkbox"
                checked={checkedItems[index] || false}
                onChange={() => handleCheckboxChange(index)}
              />
            </td>
            <td>{header}</td>
            <td>
              <CustomSelect
                index={index}
                selectedValue={selectedAttributes[index] || header} // ‚úÖ Default value is the column header
                onChange={handleSelectChange}
                options={{
                  defaultLabel: header, // ‚úÖ Default option shows the column header
                  list: mappedAttributes.map((attr) => ({
                    value: attr.backendName,
                    label: attr.uiLabel, // ‚úÖ UI Label from backend
                  })),
                }}
              />
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default StaticTable;
